import Body from "./Body";
import Card from "./Card";
import Header from "./Header";
import Text from "./Text";
import Title from "./Title";
var subs = {
  Body: Body,
  Header: Header,
  Text: Text,
  Title: Title
};
Object.keys(subs).forEach(function (key) {
  Card[key] = subs[key];
  Card[key].displayName = "Card.".concat(key);
});
export default Card;